import React, {useState} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {Card, Col, Form, Input, message, Row} from "antd";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import ApiService from "../../../../../../services/api.service";
import ImageUploadsForm from "../../../../../../components/shared-components/uploads/image-uploads.form";
import {ArrowLeftOutlined, PlusOutlined} from "@ant-design/icons";
import {useHistory} from "react-router-dom";

function CreatePages(){
    const history = useHistory();
    const [ form ] = Form.useForm()
    const [ code,setCode] = useState('')
    const [ identifier,setIdentifier] = useState('')
    const [ loading, setLoading ] = useState(false)
    function onFinish (){
        setLoading(true)
        form.validateFields()
            .then((value)=> {
                let fields = {
                    identifier: value?.identifier ?? "",
                    data : {
                        ...value?.data
                    }
                }
                message.loading({
                    content:"Loading...",
                    key:"create-city",
                    duration:2
                })
                new ApiService({
                    body: fields,
                    url:`/api/v1/master-data/airline`
                }).post()
                    .then((response)=> {
                        message.success({
                            content:response?.message ?? "Successfully created",
                            key:"create-city",
                            duration:2
                        })
                        setLoading(false)
                        form.resetFields()
                    })
                    .catch((err)=> {
                        message.info(JSON.stringify(err))
                        message.error({
                            content:err?.message  ?? "Error: Some Error",
                            key:"create-city",
                            duration:2
                        })
                        setLoading(false)
                    })
            })
            .catch((err)=> {
                message.error({
                    content:err?.message ?? "Error: Validate fields",
                    key:"create-city",
                    duration:2
                })
                setLoading(false)
            })
    }
    return (
        <React.Fragment>
            <HeroComponents
                loading={false}
                name={"Create"}
                positionName={['Master Data','Airlines']}
                thumbnail={'/create.png'}
                extra={<div className={'d-flex align-items-center mk-gap-2'}>
                    <ButtonPermission
                      permission={['admin']}
                      theme={{
                          type: "disabled",
                          property: {
                              message: "Access Denied"
                          }
                      }}
                      config={{
                          loading: loading ?? false,
                          ghost: true,
                          onClick: function(){
                              history.replace('/dashboard/master-data/airlines/flight-status')
                          },
                          icon: <ArrowLeftOutlined/>,
                          type: "primary", size: "small"
                      }}>
                        Back
                    </ButtonPermission>
                    <ButtonPermission
                      permission={['admin']}
                      theme={{
                          type: "disabled",
                          property: {
                              message: "Access Denied"
                          }
                      }}
                      config={{
                          loading: loading ?? false,
                          onClick: onFinish,
                          icon: <PlusOutlined/>,
                          type: "primary", size: "small"
                      }}>
                        Save
                    </ButtonPermission>
                </div>}
            />
            <ContainerTailwind>
                <div style={{marginTop: 90}}>
                    <Form
                        layout={'vertical'}
                        form={form}
                        initialValues={{
                            identifier: '',
                            data: {
                                display_name: "",
                                code: ""
                            },
                        }}
                    >


                        <Row gutter={24} justify={'start'}>
                            <Col span={16}>
                                <Card bordered={false}>
                                    <Row gutter={24} justify={'start'}>
                                        <Col span={12}>
                                            <Form.Item
                                                hasFeedback
                                                rules={[{required: true, message: "Can't be empty!"}]}
                                                name={['data', 'code']} label={'Code'}
                                            >
                                                <Input
                                                    placeholder={'enter your code Airlines'}
                                                    onChange={(e) => {
                                                        let value = `${e.target.value}`.replace(/ /g, '-')
                                                        form.setFieldsValue({data: {code: value.toUpperCase()}})
                                                    }}
                                                />
                                            </Form.Item>
                                        </Col>

                                    </Row>


                                    <Form.Item
                                        hasFeedback
                                        rules={[{required: true, message: "Can't be empty!"}]}
                                        name={['data', 'name']} label={'Display Name'}
                                    >
                                        <Input
                                            placeholder={'Enter a name to display in several tables '}
                                        />
                                    </Form.Item>
                                    <Form.Item
                                        hasFeedback
                                        rules={[{required: false, message: "Can't be empty!"}]}
                                        name={['data', 'description']} label={'Display Description'}
                                    >
                                        <Input.TextArea
                                            placeholder={'Enter a description '}
                                            allowClear
                                            showCount={true}
                                            autoSize={{ minRows: 4, maxRows: 6 }}
                                            size={"small"}
                                        />
                                    </Form.Item>

                                </Card>
                             </Col>
                            <Col span={8}>

                                <Card bordered={false} title={'Image Dark'}>
                                    <ImageUploadsForm
                                        config={{hidden:true}}
                                        name={['data','image','dark']}
                                        onCallback={function(src,data){
                                            form.setFieldsValue({
                                                data: {
                                                    image:{
                                                        dark: data?.url ?? null
                                                    }
                                                }
                                            })
                                        }}
                                        onDelete={function(setImage){
                                            form.setFieldsValue({
                                                data:{
                                                    image:{
                                                        dark:null
                                                    }
                                                }
                                            })
                                        }}

                                    />
                                </Card>
                                <Card bordered={false} title={'Image Light'}>
                                    <ImageUploadsForm
                                        config={{hidden:true}}
                                        name={['data','image','light']}
                                        onCallback={function(src,data){
                                            form.setFieldsValue({
                                                data: {
                                                    image:{
                                                        light: data?.url ?? null
                                                    }
                                                }
                                            })
                                        }}
                                        onDelete={function(setImage){
                                            form.setFieldsValue({
                                                data:{
                                                    image:{
                                                        light:null
                                                    }
                                                }
                                            })
                                        }}

                                    />
                                </Card>
                            </Col>

                        </Row>
                    </Form>
                </div>
            </ContainerTailwind>

        </React.Fragment>
    )
}

CreatePages.propTypes = {}
CreatePages.defaultProps = {}

export default connect(()=> {
    return {}
},{})(React.memo(CreatePages))
